import Vue from 'vue'
import Router from 'vue-router'
import login from '@/view/login'
import project from '@/view/project'
import agentaccount from '@/view/agentaccount'
import home from '@/view/home'
import test from '@/view/test'
import system from '@/view/system'
import employee from '@/view/employee'
import changepassword from '@/view/changepassword'
import modifydata from '@/view/modifydata'
import modificationrecord from '@/view/modificationrecord'
import agentreportforms from '@/view/agentreportforms'

Vue.use(Router)

export default new Router({
  routes: [
    {
      path: '/',
      component: login
    },
    {
      path: '/test',
      component: test
    },
    {
      path: '/login',
      component: login
    },
    {
      path: '/home',
      name: 'home',
      component: home,
      children: [

        {
          path: 'project',
          component: project
        },
        {
          path: 'agentaccount',
          name: 'agentaccount',
          component: agentaccount
        },
        {
          path: 'system',
          name: 'system',
          component: system
        },
        {
          path: 'employee',
          name: 'employee',
          component: employee
        },
        {
          path: 'changepassword',
          name: 'changepassword',
          component: changepassword
        },
        {
          path: 'modifydata',
          name: 'modifydata',
          component: modifydata
        },
        {
          path: 'modificationrecord',
          name: 'modificationrecord',
          component: modificationrecord
        },
        {
          path: 'agentreportforms',
          name: 'agentreportforms',
          component: agentreportforms
        },

      ]
    },

  ]
})



