import $ from 'jquery'
export default {
  ajax :function (url, params, isalert,_this) {
    var res;
    $.ajax({
      type: "POST",
      url: _this.$store.state.posturl + url,
      async: false,
      xhrFields: {
        withCredentials: true
      },
      data: params,
      success: function (response) {
        var response = JSON.parse(response);
        //session失效则直接返回
        let result = _this.routerutil.invalidsessiongologin(response,_this)
        if(result)
          return;
        //  message不是以[   {开头(非json数据)
        if (_this!=null && _this!=undefined && response.message!="" && isalert && response.message.indexOf("{") != 0 && response.message.indexOf("[") != 0 ) {
          //状态码小于0  -139:没有返回的信息
          if (response.statuscode < 0 && response.statuscode!=-139) {
            _this.$message({
             showClose: true,
             message: response.message,
             type: 'error'
             });
          } else if(response.statuscode!=-139){
            _this.$message({
              showClose: true,
              message: response.message,
              type: 'success'
            });
          }
        }
        res = response;
      }
    });
    return res;
  },

  excelajax :function (url, params, isalert,_this) {
    var res;
    $.ajax({
      type: "POST",
      headers: { 'Access-Control-Allow-Origin': '*' },
      url: _this.$store.state.posturl + url,
      async: false,
      xhrFields: {
        withCredentials: true
      },

      data: params,
      success: function (response) {
        var blob = new Blob([response], {type: "application/vnd.ms-excel"});
        var objectUrl = URL.createObjectURL(blob);
        var a = document.createElement('a');
        document.body.appendChild(a);
        a.setAttribute('style', 'display:none');
        a.setAttribute('href', objectUrl);
        var filename = "历史数据.xls";
        a.setAttribute('download', filename);
        a.click();
        URL.revokeObjectURL(objectUrl);
      }
    });

  },




}



