      //flag：   0：预定项目   1：修改预定的项目   2：确认定金   3：确认尾款
      SearchProjectAllInfoByProjectId(id,flag){
        this.TabsactiveName = 'one';
        var param = {
          project_id: id,
        }
        var res = this.requestutil.ajax('/SearchProjectAllInfoByProjectId', param, false,this);
        if(res.statuscode>=0)
          this.singleprojectinfo = JSON.parse(res.message);
        this.currentreceiveddeposittype = this.singleprojectinfo.deposit_type;
        this.singleprojectinfo.createtime = this.dateutil.timetodateymd(this.singleprojectinfo.createtime);
        let singlestatus = this.singleprojectinfo.status;
        this.singleprojectinfo.status=singlestatus==0?'AVAILABLE':singlestatus==1?'RESERVED':singlestatus==2?'SOLD':singlestatus==3?'SETTLED':'LOCKED';
        let lot_property_type = this.singleprojectinfo.lot_property_type;
        this.singleprojectinfo.lot_property_type=lot_property_type==0?'B':'H';
        this.singleprojectinfo.sunset_date = this.dateutil.timetodateymd(this.singleprojectinfo.sunset_date)
        this.singleprojectinfo.housetypefilelist = this.singleprojectinfo.house_type_pic!=undefined&&this.singleprojectinfo.house_type_pic!=""?this.singleprojectinfo.house_type_pic.split(','):[];
        this.singleprojectinfo.property_category = this.datautil.objtostr(this.singleprojectinfo.property_category);
        this.singleprojectinfo.orientation = this.datautil.objtostr(this.singleprojectinfo.orientation);
        //预定项目
        if(flag==0){
          return ;
        }
        //修改预定的项目
        if(flag==1){
          this.singleprojectinfo.contract_date = this.dateutil.timetodate(this.singleprojectinfo.contract_date)
          this.singleprojectinfo.contract_exchanged_date = this.dateutil.timetodate(this.singleprojectinfo.contract_exchanged_date)
          this.singleprojectinfo.unconditional_date = this.dateutil.timetodate(this.singleprojectinfo.unconditional_date)
          this.singleprojectinfo.pur1_dob = this.dateutil.timetodate(this.singleprojectinfo.pur1_dob)
          if(this.singleprojectinfo.pur2_dob!=undefined)
            this.singleprojectinfo.pur2_dob = this.dateutil.timetodate(this.singleprojectinfo.pur2_dob)
          this.singleprojectinfo.project_use=this.singleprojectinfo.project_use.toString();
          this.singleprojectinfo.colour_scheme=this.singleprojectinfo.colour_scheme.toString();
          this.singleprojectinfo.appliance_package=this.singleprojectinfo.appliance_package.toString();
          this.singleprojectinfo.resident_status=this.singleprojectinfo.resident_status.toString();
          this.singleprojectinfo.deposit_type=this.singleprojectinfo.deposit_type.toString();
          this.singleprojectinfo.contract_signed_by_vendors=this.singleprojectinfo.contract_signed_by_vendors.toString();
          this.singleprojectinfo.contract_signed_by_purchasers=this.singleprojectinfo.contract_signed_by_purchasers.toString();
          this.singleprojectinfo.housetypefilelist = this.singleprojectinfo.house_type_pic!=undefined&&this.singleprojectinfo.house_type_pic!=""?this.singleprojectinfo.house_type_pic.split(','):[];
          return;
        }
        //确认定金
        if(flag==2) {
          this.singleprojectinfo.upgrades_cost_on_purchaser = this.datautil.strtonumber(this.singleprojectinfo.upgrades_cost_on_purchaser);
          this.singleprojectinfo.upgrades_cost_on_vendor = this.datautil.strtonumber(this.singleprojectinfo.upgrades_cost_on_vendor);
          this.singleprojectinfo.direct_debit_deposit_received = this.datautil.strtonumber(this.singleprojectinfo.direct_debit_deposit_received);
          this.singleprojectinfo.commission_rate = this.datautil.strtonumber(this.singleprojectinfo.commission_rate);
          this.singleprojectinfo.commission_total_amount = this.datautil.strtonumber(this.singleprojectinfo.commission_total_amount);
          if(this.singleprojectinfo.acceptable!=undefined)
            this.singleprojectinfo.acceptable = this.datautil.objtostr(this.singleprojectinfo.acceptable);
          if(this.singleprojectinfo.deposit_received_date!=undefined)
            this.singleprojectinfo.deposit_received_date = this.dateutil.timetodate(this.singleprojectinfo.deposit_received_date)
          if(this.singleprojectinfo.bank_guarantee_issuer!=undefined)
            this.singleprojectinfo.bank_guarantee_issuer = this.dateutil.timetodate(this.singleprojectinfo.bank_guarantee_issuer)
          if(this.singleprojectinfo.bank_guarantee_expiry!=undefined)
            this.singleprojectinfo.bank_guarantee_expiry = this.dateutil.timetodate(this.singleprojectinfo.bank_guarantee_expiry)
          if(this.singleprojectinfo.bank_guarantee_received_date!=undefined)
            this.singleprojectinfo.bank_guarantee_received_date = this.dateutil.timetodate(this.singleprojectinfo.bank_guarantee_received_date)
          if(this.singleprojectinfo.deposit_due_date!=undefined)
            this.singleprojectinfo.deposit_due_date = this.dateutil.timetodate(this.singleprojectinfo.deposit_due_date)
          if(this.singleprojectinfo.interest_belong!=undefined)
            this.singleprojectinfo.interest_belong = this.datautil.objtostr(this.singleprojectinfo.interest_belong);
          if(this.singleprojectinfo.commission_payment_date!=undefined)
            this.singleprojectinfo.commission_payment_date = this.dateutil.timetodate(this.singleprojectinfo.commission_payment_date)

        }
        //确认尾款
        else if(flag==3){
          this.singleprojectinfo.commission_rate = this.datautil.strtonumber(this.singleprojectinfo.commission_rate);
          this.singleprojectinfo.commission_total_amount = this.datautil.strtonumber(this.singleprojectinfo.commission_total_amount);
          if(this.singleprojectinfo.commission_payment_date!=undefined)
            this.singleprojectinfo.commission_payment_date = this.dateutil.timetodate(this.singleprojectinfo.commission_payment_date);
          if(this.singleprojectinfo.settlement_date!=undefined)
            this.singleprojectinfo.settlement_date = this.dateutil.timetodate(this.singleprojectinfo.settlement_date);
          if(this.singleprojectinfo.settlement_completion!=undefined)
            this.singleprojectinfo.settlement_completion = this.datautil.objtostr(this.singleprojectinfo.settlement_completion);
        }
        else{
          this.singleprojectinfo.deposit_received_date = this.dateutil.timetodateymd(this.singleprojectinfo.deposit_received_date)
          this.singleprojectinfo.bank_guarantee_issuer = this.dateutil.timetodateymd(this.singleprojectinfo.bank_guarantee_issuer)
          this.singleprojectinfo.bank_guarantee_expiry = this.dateutil.timetodateymd(this.singleprojectinfo.bank_guarantee_expiry)
          this.singleprojectinfo.bank_guarantee_received_date = this.dateutil.timetodateymd(this.singleprojectinfo.bank_guarantee_received_date)
          this.singleprojectinfo.deposit_due_date = this.dateutil.timetodateymd(this.singleprojectinfo.deposit_due_date)
          this.singleprojectinfo.commission_payment_date = this.dateutil.timetodateymd(this.singleprojectinfo.commission_payment_date)
        }

        this.singleprojectinfo.contract_date = this.dateutil.timetodateymd(this.singleprojectinfo.contract_date)
        this.singleprojectinfo.contract_exchanged_date = this.dateutil.timetodateymd(this.singleprojectinfo.contract_exchanged_date)
        this.singleprojectinfo.unconditional_date = this.dateutil.timetodateymd(this.singleprojectinfo.unconditional_date)
        this.singleprojectinfo.pur1_dob = this.dateutil.timetodateymd(this.singleprojectinfo.pur1_dob)
        this.singleprojectinfo.pur2_dob = this.dateutil.timetodateymd(this.singleprojectinfo.pur2_dob)
        if(this.singleprojectinfo.contract_signed_by_purchasers!=undefined)
          this.singleprojectinfo.contract_signed_by_purchasers==0?this.singleprojectinfo.contract_signed_by_purchasers='signed':this.singleprojectinfo.contract_signed_by_purchasers='no signed'
