          return 'table-operate-header-cell';
        if(column.label=='PROJECT NAME'||column.label=='LOT SIZE'||column.label=='Status')
          return 'table-header-cell-1';
        if(column.label=='PROJECT ADDRESS'||column.label=='SALES PRICE')
          return 'table-header-cell-4';
        if(column.label=='LOT NO')
          return 'table-header-cell-3';
        if(column.label=='LOT SIZE')
          return 'table-header-cell-2';
