      VerifyUser: function () {
        this.conditionparam.status = undefined;
        //获取url参数来判定是不是中介员工点击了我的项目
        this.ismyproject = this.$route.query.myproject;
        this.isfinance = this.$route.query.finance;
        var res = this.requestutil.ajax('/VerifyUser', null, true, this);
        if (res.statuscode < 0)
          return;
        let message = JSON.parse(res.message);
        this.currentaccount = message.Account;
        //如果是厚易账号且要进入我的项目会直接到登录页面
        if (message.AccountType == 0 && this.ismyproject) {
          this.$goRoute('/login');
        }
        this.$store.state.author = message.Name;
        //如果是中介的话  可选择的状态要控制    查看的列要控制   按钮的显示要控制   查询条件的文本框要控制
        if (message.AccountType != 0) {
          //如果账号类型不是销售的话 是不能从我的项目中进入的
          if (message.AccountType != 2 && this.ismyproject) {
            this.$goRoute('/login');
          }
          //设置可以展示的状态
          this.conditionparam.allstatus = "0,1,3";
          this.showconditiondiv = true;
          //显示预定项目
          this.showreserve = true;
          //不可显示锁定项目
          this.showLockProject = false;
          //不可显示编辑项目
          this.showEditProject = false;
          //不可显示创建项目
          this.showCreateProject = false;
          //查询条件中展示的status  将状态指定为
          this.statusoptions = [
            {
              value: '0,1,3',
              label: 'ALLSTATUS'
            },{
              value: 0,
              label: 'AVAILABLE'
            }, {
              value: 1,
              label: 'RESERVED'
            }, {
              value: 3,
              label: 'SETTLED'
            }
          ];
          if (this.ismyproject) {
            this.conditionparam.status = 1;
            this.conditionparam.allstatus = "1,3";
            //已被预订   已经出售
            this.statusoptions = [
              {
                value: '1,3',
                label: 'ALLSTATUS'
              },
              {
                value: 1,
                label: 'RESERVED'
              }, {
                value: 3,
                label: 'SETTLED'
              }
            ];


          }
        }
        //如果是厚易管理员账号
        else if (message.Role == 0) {
          this.showreserve = false;
          //显示条件
          this.showcondition.lot_NO = false;
          //设置可以展示的状态
          this.conditionparam.allstatus = "0,1,2,3,4";
          //不可显示锁定项目
          this.showLockProject = true;
          //不可显示编辑项目
          this.showEditProject = true;
          //不可显示创建项目
          this.showCreateProject = true;
          this.statusoptions = [
            {
              value: '0,1,2,3,4',
              label: 'ALLSTATUS'
            },
            {
              value: 0,
              label: 'AVAILABLE'
            }, {
              value: 1,
              label: 'RESERVED'
            }, {
              value: 2,
              label: 'SOLD'
            }, {
              value: 3,
              label: 'SETTLED'
            }, {
              value: 4,
              label: 'LOKED'
            }
          ]
        }
        //厚易财务账号
        else if (message.Role == 1) {
          this.$goRoute('/home/project', {finance: true});
          this.showCreateProject = false;
          this.showEditProject = false;
          this.showLockProject = false;
          this.showdeposit = true;
          this.showfinalpayment = true;
          this.conditionparam.allstatus = "0,1,2,3,4";
          this.conditionparam.status = '1,2';
          this.statusoptions = [
            {
              value: '0,1,2,3,4',
              label: 'ALLSTATUS'
            },
            {
              value: '1,2',
              label: 'RESERVED&SOLD'
            },
            {
              value: 0,
              label: 'AVAILABLE'
            }, {
              value: 1,
              label: 'RESERVED'
            }, {
              value: 2,
              label: 'SOLD'
            }, {
              value: 3,
              label: 'SETTLED'
            }, {
              value: 4,
              label: 'LOKED'
            }
          ]
        }
      },
      SearchProjectAllInfoList: function (currentpage) {
        if(currentpage!=undefined)
          this.currentpage = currentpage;
        this.conditionparam.myproject = false;
        if (this.conditionparam.status != undefined) {
          this.conditionparam.statuss = this.conditionparam.status;
        } else {
          this.conditionparam.statuss = this.conditionparam.allstatus;
        }
        if (this.ismyproject) {
          this.conditionparam.myproject = true;
        }
        //计算跳过多少行
        this.conditionparam.skip = (this.currentpage - 1) * this.conditionparam.count;
        var res = this.requestutil.ajax('/SearchProjectAllInfoList', this.conditionparam, false,this);
        if (res.statuscode >= 0) {
          var message = JSON.parse(res.message);
          this.totalcount = message.totalcount;
          this.datalist = message.datalist;
        }else if (res.statuscode < 0) {
          this.totalcount = 0;
          this.datalist = [];
        }
      },
      //锁定解锁项目
      LockUnlockProject: function () {
        this.showLockProjectDialog = false;
        let param = {
          id: this.currentlockid,
          islock: this.currentislock
        }
        var res = this.requestutil.ajax('/LockUnlockProject', param, true, this);
        this.SearchProjectAllInfoList(1);
      },
      //获取当前页
      handleCurrentChange(val) {
        this.currentpage = val;

      },
