            this.createprojectformdata.sunset_date = this.dateutil.datetotime(this.createprojectformdata.sunset_datedate);
            this.createprojectformdata.house_type_pic = this.uploadfileutil.filelisttopathstr(this.housetypepiclist);
            var res = this.requestutil.ajax('/CreateProjectInfo', this.createprojectformdata, true, this);
            if(res.statuscode>=0){
              this.createprojectformdata = {
                status:'0',
              };
              this.showcreateprojectdialog = false;
              this.SearchProjectAllInfoList(1);
              this.TabsactiveName = 'one';
            }
          }
        });

      },
      EditProjectInfo(formName){
        this.$refs[formName].validate((valid) => {
          if (valid) {
            this.editprojectformdata.id = this.editprojectid;
            //时间转换为时间戳
            this.editprojectformdata.sunset_date = this.dateutil.datetotime(this.editprojectformdata.sunset_datedate);
            this.editprojectformdata.house_type_pic = this.uploadfileutil.filelisttopathstr(this.edithousetypepiclist);
            var res = this.requestutil.ajax('/EditProjectInfo', this.editprojectformdata, true, this);
            if(res.statuscode>=0){
              this.editprojectformdata = {};
              this.showeditprojectdialog = false;
              this.SearchProjectAllInfoList(1);
              this.TabsactiveName = 'one';
            }
          }
        });
      },
      //预定项目
      ReserveProject(formName){
        const loading = this.$loading({
          lock: true,
          text: 'Loading',
          spinner: 'el-icon-loading',
          background: 'rgba(0, 0, 0, 0.7)',
          target: document.querySelector('.div1')
        });
        setTimeout(() => {
          loading.close();
        }, 2000);

        this.$refs[formName].validate((valid) => {
          if (valid) {
            console.log(this.singleprojectinfo.contract_date);
            console.log(this.singleprojectinfo.contract_exchanged_date);
            console.log(this.singleprojectinfo.unconditional_date);
            this.singleprojectinfo.project_id = this.currentreserveprojectid;
            this.singleprojectinfo.contract_date = this.dateutil.datetotime(this.singleprojectinfo.contract_date);
            this.singleprojectinfo.contract_exchanged_date = this.dateutil.datetotime(this.singleprojectinfo.contract_exchanged_date);
            this.singleprojectinfo.unconditional_date = this.dateutil.datetotime(this.singleprojectinfo.unconditional_date);
            this.singleprojectinfo.pur1_dob = this.dateutil.datetotime(this.singleprojectinfo.pur1_dob);
            this.singleprojectinfo.pur2_dob = this.dateutil.datetotime(this.singleprojectinfo.pur2_dob);
            var res ;
            if(this.reserveoreditreserveprojectflag==0)
              res = this.requestutil.ajax('/ReserveProject', this.singleprojectinfo, true, this);
            if(this.reserveoreditreserveprojectflag==1){
              res = this.requestutil.ajax('/EditReserveProject', this.singleprojectinfo, true, this);
            }

            if(res.statuscode>=0){
              this.singleprojectinfo = {};
              this.showreserveprojectdialog = false;
              this.SearchProjectAllInfoList(1);
            }

          }else{
            this.$message({
              showClose: true,
              message: 'Wrong from,Please check it.',
              type: 'error'
            });
          }
        });
        loading.close();
      },
      //释放项目
      ReleaseProject(){
        if(this.ReleaseProjectDescription==''){
          this.$message({
            message: 'Description cannot be empty.',
            type: 'warning'
          });
          return;
        }
        this.showreleaseprojectdialog = false;
        var param = {
          project_id: this.currentreleaseprojectid,
          description: this.ReleaseProjectDescription
        };
        var res = this.requestutil.ajax('/ReleaseProject', param, true, this);
        this.SearchProjectAllInfoList(1);
      }

    },
    mounted(){
      this.VerifyUser();
      this.SearchProjectAllInfoList();
    }, created(){

    }
  };
</script>

<style >
  @import '../assets/iconfont/iconfont.css';
  @import '../../static/css/modulestyle.css';
  .test{
    color: red;
    background: #000;
  }
  .projectpage .el-pagination{
    margin: 0px auto;
    float: right;
  }

  .projectpage .el-input__inner{
    height: 38px;
  }


  .projectpage .search-condition{
    padding:20px 20px 0px 10px;
  }
  .projectpage .el-form-item{
    margin-left: 20px;
  }
  .projectpage .handle-button{
    right: 40px;
    position: absolute;
  }
  .projectpage .handle-button button{
    background: #2C75B8;
    color: #FFFFFF;
    font-family:PingFangSC-Semibold;
  }
  .projectpage .data-table{
    margin: 50px 10px 10px 10px;
  }
  .projectpage .el-table .warning-row {
    background: oldlace;
  }

  .projectpage .el-table .success-row {
    background: #f0f9eb;
  }

  /*这个是斑马线的颜色*/
  /*.el-table--striped .el-table__body tr.el-table__row--striped td{
    background: #F3F3F3;
  }*/

  .projectpage .el-table td, .el-table th{
    padding: 5px;
  }


  .el-carousel__item h3 {
    color: #475669;
    font-size: 18px;
    opacity: 0.75;
    line-height: 300px;
    margin: 0;
  }

  .el-carousel__item:nth-child(2n+1) {
    background-color: #d3dce6;
  }

  .singleprojectdialog .el-dialog{
    width:1100px;
  }
  .createproject-dialog .el-dialog,.editproject-dialog .el-dialog{
    width:800px;
  }

  .view-dialog .el-tab-pane{
    overflow-y: scroll;
    height:400px;
    width: 780px;
  }
  .reserveproject-dialog .el-tab-pane{
    overflow-y: scroll;
    height:350px;
    width: 800px;
  }
  .confirmproject-dialog .el-tab-pane{
    overflow-y: scroll;
    height:350px;
    width: 800px;
  }
  .createproject-dialog .el-tab-pane,.editproject-dialog .el-tab-pane{
    overflow-y: scroll;
    height:350px;
    width: 500px;
  }
  .confirmproject-dialog .el-carousel__container,.reserveproject-dialog  .el-carousel__container{
    height: 350px;
  }

  .view-dialog .el-carousel__item,.confirmproject-dialog .el-carousel__item,.reserveproject-dialog .el-carousel__item{
    background-color:#fff;
  }
  .view-dialog .el-carousel__container{
    height:400px;
  }
  .reserveproject-dialog .el-tabs__content{
    width:800px;
  }

</style>



